.card {
  position: relative;
  width: 380px;
  padding: 48px;
  padding-top: 236px;
  color: #1f3f68;
  letter-spacing: 0.05em;
  border-radius: 10px;
  box-shadow: 5px 20px 50px rgba(16, 112, 177, 0.2);

  &::before {
    content: "";
    position: absolute;
  }

  &--bring {
    &::before {
      top: -20px;
      left: 78px;
      width: 255px;
      height: 208px;
      background-image: url("../../assets/images/advantage__bring.png");
    }
  }

  &--make {
    &::before {
      top: 48px;
      left: 74px;
      width: 192px;
      height: 132px;
      background-image: url("../../assets/images/advantage__make.png");
    }
  }

  &--build {
    &::before {
      top: 49px;
      left: 100px;
      width: 171px;
      height: 132px;
      background-image: url("../../assets/images/advantage__build.png");
    }
  }
}

.card__title {
  font-size: 20px;
  line-height: 30px;
}

.card__description {
  opacity: 0.6;
}

.card__btn-learn-more {
  position: relative;
  padding-right: 44px;
  color: $bg-color-light;
  font-size: 14px;
  line-height: 30px;
  text-decoration: none;

  &:hover {
    color: $bg-color-dark;
  }
}

.card__btn-icon {
  position: absolute;
  top: 0;
  right: 0;
  width: 16px;
  height: 16px;
  background-image: url("../../assets/icons/card__arrow.svg");
}
