.header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 1366px;
  margin: 0 auto;
}

.header__logo {
  margin-top: 48px;
  margin-left: 83px;
}

.header__navigation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 24px;
  height: 16px;
  margin-top: 56px;
  margin-right: 87px;
}

.header__burger {
  width: 24px;
  height: 16px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.header__burger-line {
  position: relative;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #356ead;

  &::before {
    content: "";
    position: absolute;
    top: -7px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #356ead;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #356ead;
  }
}
